'use strict';
(function () {
    angular
        .module('app.articles')
        .controller('Article', ['$sce', 'article', 'articleFactory', 'comments', 'nbComments', Article]);

    function Article($sce, article, articleFactory, comments, nbComments) {
        /***************
         * DECLARATIONS
         **************/
        var vm = this;
        vm.recipe = article;
        vm.comments = comments;
        vm.nbComments = nbComments;

        _init();

        function _init() {
            articleFactory.insertView(vm.recipe.id).then(function() {
                angular.forEach(vm.comments, function (comment) {
                    comment.text = $sce.trustAsHtml(comment.text);
                });
            });
        }
    }
})();
