/**
 * @ngdoc filter
 * @name moment
 *
 * @description
 *  Dates filter using moment.js
 *
 * */
'use strict';
(function () {
    angular.module('app.articles')
        .filter('moment', [momentFilter]);

    function momentFilter() {
        return function(input) {
            return moment(input + '', 'x').format('D MMMM YYYY');
        }
    }
})();