'use strict';
(function () {
    angular.module('app.articles')
        .factory('articleFactory', ['$http', ArticleFactory]);

    function ArticleFactory($http) {
        /***************
         * DECLARATIONS
         **************/
        var factory = {
            //Variables

            //Functions
            fetchArticles: fetchArticles,
            fetchArticleContent: fetchArticleContent,
            insertView: insertView
        };

        return factory;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function fetchArticles (page) {
            return $http.get(host_url + 'api/articles/' + page).then(function (response) {
                return response.data;
            });
        }

        function fetchArticleContent (articleId) {
            var req = {
                method: 'GET',
                url: host_url + 'api/articles/article/' + articleId,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req).then(function (response) {
                return response.data.article[0];
            });
        }

        function insertView (articleId) {
            return $http.post(host_url + 'api/articles/article/' + articleId + '/view');
        }
    }
})();

