'use strict';
(function () {
    angular
        .module('app')
        .controller('Menu', ['$location', '$state', Menu]);

    function Menu($location, $state) {

        /***************
         * DECLARATIONS
         **************/

        var viewModel = this;
        viewModel.goToHome    = goToHome;
        viewModel.goToContact = goToContact;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function goToHome() {
            $state.go('home');
        }

        function goToPartners() {}

        function goToContact() {
            $state.go('contact');
        }
    }
})();
