(function () {
    'use strict';

    angular
        .module('app.contact')
        .config(contactRoute);

    function contactRoute ($stateProvider) {
        $stateProvider
            .state('contact', {
                url: '/contact',
                views: {
                    '@': {
                        templateUrl: 'contact/views/contact.html',
                        title: 'contact'
                    }
                }
            });
    }
})();
