'use strict';
(function () {
    angular.module('app.home', [
        'ngSanitize',
        'ngMaterial'
    ]);
})();

(function () {
    'use strict';

    angular
        .module('app.home')
        .config(routerConfig);

    function routerConfig ($stateProvider, $urlRouterProvider) {

        function _transitionToEntryPoint ($state) {
            $state.transitionTo('home');
        }
        _transitionToEntryPoint.$inject = ["$state"];

        $urlRouterProvider
            .when('', _transitionToEntryPoint)
            .otherwise('/404');

        $stateProvider
            .state('entryPoint', {
                url: '/redirect',
                controller: _transitionToEntryPoint
            })
            .state('home', {
                url: '/',
                views: {
                    //header: {
                    //    templateUrl: 'views/partials/login/header.html'
                    //},
                    '@': {
                        templateUrl: 'home/views/home.html',
                        controller: 'Home',
                        controllerAs: 'ctrl',
                        title: 'home',
                        resolve: {
                            articles: ["articleFactory", function (articleFactory) {
                                return articleFactory.fetchArticles(1).then(function(data) {
                                   return data.articles;
                                });
                            }],
                            pagination: ["articleFactory", function (articleFactory) {
                                return articleFactory.fetchArticles(1).then(function(data) {
                                    return data.pagination;
                                });
                            }]
                        }
                    }
                }
            })
            .state('404', {
                url: '/404',
                views: {
                    '@': {
                        templateUrl: 'common/views/404.html'
                    }
                }
            });
    }
    routerConfig.$inject = ["$stateProvider", "$urlRouterProvider"];
})();
'use strict';
(function () {
    angular.module('app.home')
        .factory('homeFactory', ['$http', HomeFactory]);

    function HomeFactory($http) {
        /***************
         * DECLARATIONS
         **************/
        var factory = {
            //Variables

            //Functions
            fetchArticlesContent: fetchArticlesContent
        };

        return factory;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function fetchArticlesContent() {
            var req = {
                method: 'GET',
                url: host_url + 'api/articles',
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req);
        }
    }
})();


'use strict';
(function() {
    angular
        .module('app.home')
        .controller('Home', ['$log', '$location', '$window', 'articleFactory', 'articles', 'pagination', Home]);

    function Home($log, $location, $window, articleFactory, articles, pagination) {

        $log.debug('[app.home] Loading Home controller');

        /***************
         * DECLARATIONS
         **************/

        var viewModel = this;
        viewModel.articles = articles;
        viewModel.pagination = pagination;

        viewModel.goToArticle = goToArticle;
        viewModel.nextPage = nextPage;
        viewModel.previousPage = previousPage;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function goToArticle(articleId) {
            $location.path('article/' + articleId);
            $window.scrollTo(0, 0);
        }

        function nextPage() {
            var currentPage = viewModel.pagination.currentPage;
            articleFactory.fetchArticles(currentPage + 1).then(function(data) {
                viewModel.articles = data.articles;
                viewModel.pagination = data.pagination;
                $window.scrollTo(0, 0);
            });
        }

        function previousPage() {
            var currentPage = viewModel.pagination.currentPage;
            articleFactory.fetchArticles(currentPage - 1).then(function(data) {
                viewModel.articles = data.articles;
                viewModel.pagination = data.pagination;
                $window.scrollTo(0, 0);
            });
        }
    }
})();

