(function () {
    'use strict';

    angular
        .module('app.articles')
        .config(articleRoute);

    function articleRoute ($stateProvider) {
        $stateProvider
            .state('articles', {
                url: '/article/:articleId',
                views: {
                    '@': {
                        templateUrl: 'articles/views/article.html',
                        controller: 'Article',
                        controllerAs: 'viewModel',
                        title: 'article',
                        resolve: {
                            article: ["$stateParams", "articleFactory", function ($stateParams, articleFactory) {
                                return articleFactory.fetchArticleContent($stateParams.articleId);
                            }],
                            comments: ["$stateParams", "commentsService", function ($stateParams, commentsService) {
                                return commentsService.getCommentsByArticle($stateParams.articleId).then(function(data) {
                                    return data.comments;
                                });
                            }],
                            nbComments: ["$stateParams", "commentsService", function ($stateParams, commentsService) {
                                return commentsService.getCommentsByArticle($stateParams.articleId).then(function(data) {
                                    return data.totalComments;
                                });
                            }]
                        }
                    }
                }
            });
    }
    articleRoute.$inject = ["$stateProvider"];
})();

/**
 * @ngdoc filter
 * @name moment
 *
 * @description
 *  Dates filter using moment.js
 *
 * */
'use strict';
(function () {
    angular.module('app.articles')
        .filter('moment', [momentFilter]);

    function momentFilter() {
        return function(input) {
            return moment(input + '', 'x').format('D MMMM YYYY');
        }
    }
})();
'use strict';
(function () {
    angular.module('app.articles')
        .factory('articleFactory', ['$http', ArticleFactory]);

    function ArticleFactory($http) {
        /***************
         * DECLARATIONS
         **************/
        var factory = {
            //Variables

            //Functions
            fetchArticles: fetchArticles,
            fetchArticleContent: fetchArticleContent,
            insertView: insertView
        };

        return factory;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function fetchArticles (page) {
            return $http.get(host_url + 'api/articles/' + page).then(function (response) {
                return response.data;
            });
        }

        function fetchArticleContent (articleId) {
            var req = {
                method: 'GET',
                url: host_url + 'api/articles/article/' + articleId,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req).then(function (response) {
                return response.data.article[0];
            });
        }

        function insertView (articleId) {
            return $http.post(host_url + 'api/articles/article/' + articleId + '/view');
        }
    }
})();


'use strict';
(function () {
    angular
        .module('app.articles')
        .controller('Article', ['$sce', 'article', 'articleFactory', 'comments', 'nbComments', Article]);

    function Article($sce, article, articleFactory, comments, nbComments) {
        /***************
         * DECLARATIONS
         **************/
        var vm = this;
        vm.recipe = article;
        vm.comments = comments;
        vm.nbComments = nbComments;

        _init();

        function _init() {
            articleFactory.insertView(vm.recipe.id).then(function() {
                angular.forEach(vm.comments, function (comment) {
                    comment.text = $sce.trustAsHtml(comment.text);
                });
            });
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('app')
        .controller('CommentsController', ['$sanitize', 'commentsService', 'toastr', CommentsController]);

    function CommentsController ($sanitize, commentsService, toastr) {
        var vm = this;

        vm.comment = {
            pseudo: '',
            mail: '',
            url: '',
            text: ''
        };
        vm.sendComment = sendComment;

        function sendComment () {
            var comment = $sanitize(vm.comment.text);
            vm.comment.text = comment.replace(/\n/g, '<br />');
            commentsService.sendComment(vm.comment, vm.articleId).then(function () {
                vm.comment = {
                    pseudo: '',
                    mail: '',
                    url: '',
                    text: ''
                };
                commentsService.getCommentsByArticle(vm.articleId).then(function (data) {
                    vm.comments = data.comments;
                    vm.nbComments = data.totalComments;
                });
                toastr.success('Commentaire envoyé !');
            });
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('app.articles')
        .directive('comments', commentsDirective);

    function commentsDirective () {
        return {
            bindToController: true,
            controller: 'CommentsController',
            controllerAs: 'ctrl',
            link: function link (scope) {},
            restrict: 'E',
            scope: {
                comments: '=',
                nbComments: '=',
                articleId: '='
            },
            templateUrl: 'articles/comments/views/comments.html'
        };
    }
})();

(function () {
    'use strict';

    angular
        .module('app.articles')
        .service('commentsService', commentsService);

    function commentsService ($http) {
        function getCommentsByArticle (articleId) {
            return $http.get(host_url + 'api/comments/' + articleId)
                .then(function (response) {
                    return response.data;
                });
        }


        function sendComment (comment, articleId) {
            //return $http.post(host_url + 'api/comments/' + articleId, comment)
            //    .then(function (response) {
            //        return {}; // response.data;
            //    });
            return $http({
                url: host_url + 'api/comments/' + articleId,
                method: 'POST',
                params: comment,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            });
        }
        return {
            getCommentsByArticle: getCommentsByArticle,
            sendComment: sendComment
        };
    }
    commentsService.$inject = ["$http"];
})();
