(function () {
    'use strict';

    angular
        .module('app.home')
        .config(routerConfig);

    function routerConfig ($stateProvider, $urlRouterProvider) {

        function _transitionToEntryPoint ($state) {
            $state.transitionTo('home');
        }

        $urlRouterProvider
            .when('', _transitionToEntryPoint)
            .otherwise('/404');

        $stateProvider
            .state('entryPoint', {
                url: '/redirect',
                controller: _transitionToEntryPoint
            })
            .state('home', {
                url: '/',
                views: {
                    //header: {
                    //    templateUrl: 'views/partials/login/header.html'
                    //},
                    '@': {
                        templateUrl: 'home/views/home.html',
                        controller: 'Home',
                        controllerAs: 'ctrl',
                        title: 'home',
                        resolve: {
                            articles: function (articleFactory) {
                                return articleFactory.fetchArticles(1).then(function(data) {
                                   return data.articles;
                                });
                            },
                            pagination: function (articleFactory) {
                                return articleFactory.fetchArticles(1).then(function(data) {
                                    return data.pagination;
                                });
                            }
                        }
                    }
                }
            })
            .state('404', {
                url: '/404',
                views: {
                    '@': {
                        templateUrl: 'common/views/404.html'
                    }
                }
            });
    }
})();