(function () {
    'use strict';

    angular
        .module('app.articles')
        .config(articleRoute);

    function articleRoute ($stateProvider) {
        $stateProvider
            .state('articles', {
                url: '/article/:articleId',
                views: {
                    '@': {
                        templateUrl: 'articles/views/article.html',
                        controller: 'Article',
                        controllerAs: 'viewModel',
                        title: 'article',
                        resolve: {
                            article: function ($stateParams, articleFactory) {
                                return articleFactory.fetchArticleContent($stateParams.articleId);
                            },
                            comments: function ($stateParams, commentsService) {
                                return commentsService.getCommentsByArticle($stateParams.articleId).then(function(data) {
                                    return data.comments;
                                });
                            },
                            nbComments: function ($stateParams, commentsService) {
                                return commentsService.getCommentsByArticle($stateParams.articleId).then(function(data) {
                                    return data.totalComments;
                                });
                            }
                        }
                    }
                }
            });
    }
})();
