(function () {
    'use strict';

    angular
        .module('app.articles')
        .directive('comments', commentsDirective);

    function commentsDirective () {
        return {
            bindToController: true,
            controller: 'CommentsController',
            controllerAs: 'ctrl',
            link: function link (scope) {},
            restrict: 'E',
            scope: {
                comments: '=',
                nbComments: '=',
                articleId: '='
            },
            templateUrl: 'articles/comments/views/comments.html'
        };
    }
})();
